import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';
import { Row, Col, Table, Card, CardTitle, CardBody } from "reactstrap";

const Tables = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + '/P1data_2023.csv')
      .then(response => response.text())
      .then(csvData => {
        const parsedData = Papa.parse(csvData, { header: true }).data;
        setData(parsedData);
      });
  }, []);
  return (
    <Row>
      {/* --------------------------------------------------------------------------------*/}
      {/* table-2*/}
      {/* --------------------------------------------------------------------------------*/}
      <Col lg="12">
        <Card>
          <CardTitle tag="h3" className="border-bottom p-3 mb-0">
            2023 정기노사협의 안건
          </CardTitle>
          <CardBody className="">
           <Table bordered striped>
              <thead>
                <tr>
                  <th>분기</th>
                  <th>내용</th>
                  <th>비고</th>
                  <th>진행사항</th>
                </tr>
              </thead>
              <tbody>
                {data.map((item, index) => (
                <tr key={index}>
                  <td>{item.분기}</td>
                  <td>{item.내용}</td>
                  <td>{item.비고}</td>
                  <td>{item.진행사항}</td>
                </tr>
                ))}
              </tbody>
            </Table>
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
};

export default Tables;
