import { Col, Row } from "reactstrap";
import OfficialTables from "../components/dashboard/OfficialTables";
import ProjectTables1 from "./ui/P1Tables_2023";
import ProjectTables2 from "./ui/P2Tables_2023";

const Starter = () => {
  return (
    <div>
      {/***ProjectTables1***/}
      <Row>
      <Col lg="12">
          <ProjectTables1/>
        </Col>
      </Row>
      {/***ProjectTables2***/}
      <Row>
      <Col lg="12">
          <ProjectTables2/>
        </Col>
      </Row>
      {/***OfficialTables***/}
      <Row>
        <Col lg="12">
          <OfficialTables/>
        </Col>
      </Row>
    </div>
  );
};

export default Starter;
